@import '../../scssMixin/generateClassName';
//定义了em单位的缩进
//后缀"-imprtant"提高权重

@mixin setSizes($size){
    @include z('text-indent-'+$size,$global:true){
        text-indent: $size+em;
    }
    @include z('text-indent-'+$size+'important',$global:true){
        text-indent: $size+em!important;
    }
}
// 12-100px的字大小 className  :   z-font-size-12  
@for $i from 1 through 10 {
    @include setSizes($i);
}